<template>
  <v-app>
    <top-toolbar />

    <app-header />

    <v-main class="main">
      <v-container fluid class="pa-0" :class="bodyColor">
        <v-sheet
          color="accent3"
          height="38"
          width="100%"
          elevation="4"
        ></v-sheet>

        <router-view class="pa-3" />

        <v-sheet
          v-if="showBottomSheet"
          color="accent3"
          height="38"
          width="100%"
          class="bottom-sheet"
        ></v-sheet>
      </v-container>
    </v-main>

    <app-footer />
  </v-app>
</template>

<script>
import AppHeader from "./components/app_markup/AppHeader";
import AppFooter from "./components/app_markup/AppFooter";
import TopToolbar from "./components/app_markup/TopToolbar";
export default {
  name: "App",
  components: { TopToolbar, AppFooter, AppHeader },
  computed: {
    bodyColor() {
      let name = this.$route.name;
      if (name === "Events") return "primary5";
      else return "primary2";
    },

    showBottomSheet() {
      let name = this.$route.name;
      if (name === "Events") return true;
      else return false;
    }
  }
};
</script>

